import { requestPost } from '@engine/crud';

/**
 * 数据入库
 */
export function dbCommit(
    opt: dbCommitConfig,
    parameters: { [widgetName: string]: Array<string> | number | string } = {},
) {
    return requestPost('db/commit', {
        data: {
            __parameters__: { ...opt, ...parameters },
        },
        encoder: v => encodeURIComponent(v),
    });
}

export interface dbCommitConfig {
    jsConfId: string;
    callback: string;
    callbackConfId: string;
}
